import skyRaty from "../../components/skyraty.vue";
var shopid = 0;
export default ({
	components: {
		skyRaty
	},
	data: function() {
		return {
			shop: {},
			pageData: {},
			couponList:{},
			pageLoad: true,
			catid: 0,
			page: "product",
			ratyPage: {},
			detailPage: {},
			ksShow: false,
			isFav:false,
			ksproduct: [],
			ksList: [],
			ksList2: [],
			ksid: 0,
			ksid1: 0,
			ksid2: 0,
			headerMoreClass: ""
		}
	},
	onLoad: function(ops) {
		shopid = ops.shopid;
		this.getPage();
		this.getProduct();
		uni.setNavigationBarColor({
			frontColor: '#ffffff',
			backgroundColor: '#333333',
			animation: {
				duration: 400,
				timingFunc: 'easeIn'
			}
		})
	},
	methods: {
		favShopToggle:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"?fromapp=wxapp&m=fav&a=toggle&ajax=1",
				data:{
					objectid:shopid,
					tablename:"mod_b2b_shop"
				},
				success:function(res){
					if(res.data=='delete'){
						that.isFav=false;
					}else{
						that.isFav=true;
					}
					 
				}
			})
			 
		},
		headerMoreShow: function() {
			this.headerMoreClass = 'flex-col';
		},
		headerMoreHide: function() {
			this.headerMoreClass = '';
		},
		goOrder: function() {
			uni.navigateTo({
				url: "../b2b_order/confirm?shopid=" + shopid
			})
		},
		goGuest: function() {
			uni.navigateTo({
				url: "../b2b_guest/user?shopid=" + shopid
			})

		},
		goProduct: function(id) {
			uni.navigateTo({
				url: "../b2b_product/show?id=" + id + "&shopid=" + shopid
			})

		},
		getPage: function() {
			var that = this;
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2b_shop&shopid=" + shopid,
				success: function(res) {
					uni.setNavigationBarTitle({
						title:res.data.shop.shopname
					})
					that.couponList=res.data.couponList;
					that.shop = res.data.shop;
					that.isFav=res.data.isfav;
					that.pageLoad = true;
				}
			});
		},
		getProduct: function() {
			var that = this;
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2b_product&ajax=1&shopid=" + shopid,
				data: {
					catid: that.catid
				},
				dataType: "json",
				success: function(res) {
					that.pageData = res.data;
					that.pageLoad = true;
				}
			})
		},
		updateCartStat: function() {
			var that = this;
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2b_product&ajax=1&shopid=" + shopid,
				data: {
					catid: that.catid
				},
				dataType: "json",
				success: function(res) {
					that.pageData.cart_total_num = res.data.cart_total_num;
					that.pageData.cart_total_money = res.data.cart_total_money;

				}
			})
		},
		getRaty: function() {
			var that = this;
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2b_shop&a=raty&ajax=1&shopid=" + shopid,
				dataType: "json",
				success: function(res) {
					that.ratyPage = res.data;
					that.pageLoad = true;
				}
			})
		},
		getDetail: function() {
			var that = this;
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2b_shop&a=detail&ajax=1&shopid=" + shopid,
				dataType: "json",
				success: function(res) {
					that.detailPage = res.data;
					that.pageLoad = true;
				}
			})
		},
		setCat: function(catid) {
			this.catid = catid;
			this.getProduct();
		},
		setPage: function(page) {
			this.page = page;
			this.pageLoad = false;
			console.log(page)
			switch (page) {
				case "raty":
					this.getRaty();
					break;
				case "detail":
					this.getDetail();
					break;
				default:
					this.getProduct();
					break;
			}
		},
		addCart: function(id, ksid) {
			var that = this;
			var id = id;
			var ksid = ksid == undefined ? 0 : ksid;
			var amount = 1;
			this.app.get({
				url: this.app.apiHost + '/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: id,
					amount: amount,
					shopid: shopid,
					ksid: ksid
				},
				method: 'GET',
				dataType: "json",
				success: function(res) {
					if (res.error) {
						uni.showToast({
							title: res.message
						})
						return false;
					}
					that.updateCartStat();
					var list = that.pageData.list;
					for (var i = 0; i < list.length; i++) {
						if (list[i].id == id) {
							if (res.data.amount > 0) {
								list[i].incart = 1;
							}
							list[i].cart_amount = res.data.amount;
							break;
						}
					}
					that.pageData.list = that.parseList(list);
				}
			})
		},
		plusCart: function(id, amount, ksid) {
			var that = this;
			var id = id;
			var amount = amount;
			var ksid = ksid == undefined ? 0 : ksid;
			amount++;
			this.app.get({
				url: this.app.apiHost + '/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: id,
					shopid: shopid,
					amount: amount,
					ksid: ksid
				},
				method: 'GET',
				dataType: "json",

				success: function(res) {
					if (res.error) {
						uni.showToast({
							title: res.message
						})
						return false;
					}
					that.updateCartStat();
					var list = that.pageData.list;
					for (var i = 0; i < list.length; i++) {
						if (list[i].id == id) {

							list[i].cart_amount = res.data.amount;
							break;
						}
					}

					that.pageData.list = that.parseList(list);
				}
			})
		},
		minusCart: function(id, amount, ksid) {
			var that = this;
			var id = id;
			var amount = amount;
			var ksid = ksid == undefined ? 0 : ksid;
			amount--;
			var isdelete = 0;
			if (amount == 0) {
				isdelete = 1
			}
			this.app.get({
				url: this.app.apiHost + '/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: id,
					amount: amount,
					shopid: shopid,
					ksid: ksid,
					isdelete: isdelete
				},
				method: 'GET',
				dataType: "json",

				success: function(res) {
					if (res.error) {
						uni.showToast({
							title: res.message
						})
						return false;
					}
					that.updateCartStat();
					var list = that.pageData.list;
					for (var i = 0; i < list.length; i++) {
						if (list[i].id == id) {
							if (res.data.amount == 0) {
								list[i].incart = 0;
							}
							list[i].cart_amount = res.data.amount;
							break;
						}
					}

					that.pageData.list = that.parseList(list);
				}
			})
		},
		parseList: function(list) {
			for (var i = 0; i < list.length; i++) {
				if (list[i].cart_num > 0) {
					list[i].cart_num_class = "prolist-item-row-cart-active";
				} else {
					list[i].cart_num_class = "";
				}
			}
			return list;
		},
		//规格操作
		ksBox: function(id) {
			var that = this;
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2b_product_ks&ajax=1&productid=" + id,
				dataType: "json",
				success: function(res) {
					that.ksShow = true;
					that.ksproduct = res.data.product;
					that.ksList = res.data.ksList;
					that.ksList2 = res.data.ksList2;
					that.ksid1 = res.data.ksid;
					that.ksid = res.data.ksid;
				}
			})
		},
		ksHide: function() {
			this.ksid = 0;
			this.ksShow = false;
		},
		ks1: function(id) {
			var that = this;
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2b_product_ks&a=sizeList&ajax=1&id=" + id,
				dataType: "json",
				success: function(res) {
					that.ksid1 = res.data.ksid;
					that.ksid = res.data.ksid;
					that.ksproduct = res.data.product;
					that.ksList2 = res.data.ksList2;
				}
			})
		},
		ks2: function(id) {
			var that = this;
			that.ksid = id;
			this.app.get({
				url: this.app.apiHost + "/module.php?m=b2b_product_ks&a=get&ajax=1&id=" + id,
				dataType: "json",
				success: function(res) {

					that.ksproduct = res.data.product;

				}
			})
		},
		ksAddCart: function(id) {
			var that = this;
			var id = id;
			var ksid = that.ksid;
			var amount = 1;
			this.app.get({
				url: this.app.apiHost + '/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: id,
					shopid: shopid,
					amount: amount,
					ksid: ksid
				},
				method: 'GET',
				dataType: "json",
				success: function(res) {
					if (res.error) {
						uni.showToast({
							title: res.message
						})
						return false;
					}
					that.updateCartStat();
					that.ksproduct.incart = 1;
					that.ksproduct.cart_amount = res.data.amount;
				}
			})
		},
		ksPlusCart: function(id, amount) {
			var that = this;
			var id = id;
			var amount = amount;
			var ksid = that.ksid;
			amount++;
			this.app.get({
				url: this.app.apiHost + '/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: id,
					shopid: shopid,
					amount: amount,
					ksid: ksid
				},
				method: 'GET',
				dataType: "json",

				success: function(res) {
					if (res.error) {
						uni.showToast({
							title: res.message
						})
						return false;
					}
					that.updateCartStat();
					that.ksproduct.cart_amount = res.data.amount;
				}
			})
		},
		ksMinusCart: function(id, amount) {
			var that = this;
			var id = id;
			var amount = amount;
			var ksid = that.ksid;
			amount--;
			var isdelete = 0;
			if (amount == 0) {
				isdelete = 1
			}
			this.app.get({
				url: this.app.apiHost + '/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: id,
					amount: amount,
					shopid: shopid,
					ksid: ksid,
					isdelete: isdelete
				},
				method: 'GET',
				dataType: "json",

				success: function(res) {
					if (res.error) {
						uni.showToast({
							title: res.message
						})
						return false;
					}
					that.updateCartStat();
					if (res.data.amount == 0) {
						that.ksproduct.cart_amount = 0;
						that.ksproduct.incart = 0;
					} else {
						that.ksproduct.cart_amount = res.data.amount;
					}

				}
			})
		}
	}
})
